import React from 'react'
import {Helmet} from "react-helmet";
import PageTransition from 'gatsby-plugin-page-transitions'
import { Link } from "gatsby"
import Header from '../components/header'
import Cta from '../components/Cta'
import Footer from '../components/footer'

import lexicrea_img_Illustr_flanner from '../images/lexicrea_img_Illustr_flanner.png';


let pathPrefix = `/`



export default () => (
    <PageTransition>
    <div className="layoutblocksContainer">
        <Helmet>
            <meta charSet="utf-8" />
            <link rel="canonical" href="https://www.lexiquedelacreativite.com" />
            <title>Lexique de la Créativité - Imagination, Intuition et Créativité</title>
            <meta name="description" content="Explorez les rôles de l'imagination et de l'intuition dans le processus créatif, ainsi que leur influence sur la pensée innovante." />

        </Helmet>

        <div className="layoutblocksheaderSidebar">
            <Header/>
        </div>

        <div className="layoutblocksbodyMain">
        <Cta/>
            <div className="section">

            <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">I</p>
                        <h2>Imagination</h2>
                        <h3>Daydream... I fell asleep amid the flowers...</h3>
                        <p className="text-left content-project">Souvent, nous projetons des situations que nous allons bientôt vivre (un rendez-vous important, une échéance...). Parfois aussi, nous laissons notre esprit vagabonder, de rêverie en rêverie… Nous le faisons sans même nous en rendre compte. Il en est de même pour le créateur, lorsqu’il cherche l’inspiration.
                        </p>
                        <p className="text-left content-project">
                        Il utilise ce monde entre rêve et réalité pour sonder ses pensées dans de nombreuses phases du processus créatif. Cette zone aux contours mal définis, en mouvement constant, dans laquelle s’effectue des combinaisons et des projections du produit créatif en construction créé un état de conscience particulier.
                        </p>
                        <h3>L'énergie noire du cerveau</h3>
                        <p className="text-left content-project">De récentes études ont révélé que le cerveau d'une personne au repos reste très actif et consomme une énergie aussi grande que celle utilisée par le cerveau conscient. On soupçonne qu'il orchestre la façon dont le cerveau organise les données et les souvenirs de même qu'il coordonne les différents modules cérébraux participant à la programmation des tâches. Il préparerait ainsi le cerveau à passer d'un mode non conscient à une activité consciente.</p>
                        <p className="text-left content-project">Cet état, même s'il est décrit par Freud comme "infantile", s'il est bien utilisé, est un atout pour la créativité. Ceux qui laissent vagabonder leur esprit tout en gardant le contrôle trouverons des esquisses de solution à leurs problèmes créatifs...</p>
                        <p className="text-left content-project">L’imagination se différencie du rêve car elle implique une action consciente de la part de l’individu. Elle consiste à organiser et combiner les images qui apparaissent à la conscience pour former une nouvelle représentation. C’est un processus qui se nourrit du vécu du créateur, de ses sensations comme de ses raisonnements.</p>
                        <h3>Nourrir l'imagination</h3>
                        <p className="text-left content-project">
                        Pour profiter pleinement de son imagination, le créateur doit maintenir une curiosité sur la vie dans son ensemble et non seulement dans son domaine d’activité. En provoquant consciemment l’intérêt, le subconscient accumulera ces informations et aidera l’individu à les combiner pour créer de nouvelles représentations. Goethe donnait à un poète en devenir les conseils suivants :
                        </p>
                        <div className="row align-center">
                            <div className="small-11 medium-9 columns">
                                <h2 className="project-quote">« Vous me semblez partager l’erreur commune à beaucoup de jeunes gens, qui croient devoir s’occuper exclusivement de poésie ; et cependant un vrai poète, dont la vocation est incontestable, puise dans la vie et dans la science la matière sans laquelle ses œuvres seraient vides. Selon moi, tout au contraire, vous n’avez rien à perdre si vous vous adonnez à la vie active ou à la science, car vous ne serez sûr de votre talent qu’après avoir consacré beaucoup de temps à l’une ou l’autre de ces fins. Si votre inspiration s’empare avec force de toute l’expérience et de toutes les connaissances que vous aurez accumulées, si elle réussit à ramener à l’unité tous ces différents éléments opposés, l’événement que vous semblez appeler de tous vos vœux se produira de lui-même, et rien d’autre ne saurait le faire naître. »
                                    <p className="project-quote-footer">Goethe</p></h2>

                            </div>
                        </div>
                        <p className="text-left content-project">Pour qu’elle soit productive, l’imagination doit être provoquée et excitée. Elle s’alimente de tout. Plus vous élargirez vos horizons, plus elle sera disposée à vous faire des propositions créatives originales. Tentez de prendre du recul sur ce que vous vivez. Réfléchissez, analysez, comparez. Regardez de tous vos yeux (J.Verne).</p>
                        <h3>S'entourer</h3>
                        <p className="text-left content-project">Enfin, variez vos connaissances et trouvez des personnes avec qui vous pourrez vous exprimer sans vous freiner. Si la solitude est nécessaire pour concrétiser ses idées, le contact social alimente aussi le moteur de l’imagination.</p>
                        <p className="text-left content-project">Une fois sa quête satisfaite, le créateur revient au réel pour vérifier ou exécuter la pensée qui lui est apparue. Ce retour au réel après la fuite dans l’imaginaire est primordial et concrétise la créativité. Sans ce réveil nous nous approcherions de l’état pathologique qui ne permet pas de créer. Pour conclure, quelques mots de Marthe Seguin Fontes (dans le second souffle de la créativité) :</p>
                        <div className="row align-center">
                            <div className="small-11 medium-9 columns">
                                <h2 className="project-quote">Parti du réel, l’acte de créer retourne au réel, en donnant naissance à des produits qui vont à leur tour s’insérer dans les réalités du monde.
                                    <p className="project-quote-footer">Marthe Seguin Fontes (dans le second souffle de la créativité) </p></h2>

                            </div>
                        </div>
                        <p className="text-left content-project">Parti du réel, l’acte de créer retourne au réel, en donnant naissance à des produits qui vont à leur tour s’insérer dans les réalités du monde.</p>
                        <p className="text-left content-project">C’est peut-être par ce moyen que le créateur, découvrant son pouvoir de participer à la vie et de s’intégrer au cosmos, qu’il modifie peu à peu, trouve un meilleur équilibre.</p>
                    </div>
                </div>
                <img className="articleIllustration" src={lexicrea_img_Illustr_flanner} alt="imagination et créativité" />
            <div className="row align-center padding5_top">
                    <div className="small-11 medium-8 columns padding5_top">
                        <p className="letterCapitalcontent">I</p>
                        <h2>Intuition</h2>
                        <p className="text-left content-project">L’intuition est la connaissance directe, immédiate de la vérité ou de l’existence de quelque chose sans recours au raisonnement, à l’expérience. C’est une sensation, souvent indescriptible par les mots, d’un chemin à prendre, d’une direction vers laquelle aller. Nous utilisons l’intuition quotidiennement que ce soit en faisant des achats ou pour savoir si quelqu’un est digne de confiance. 90% de nos opérations mentales seraient inconscientes. Pour David Lynch, l’intuition est « la connaissance, le savoir, un flux d’idées permettant de voir ce qui marche ou ne marche pas, et de résoudre les problèmes. », « C’est l’outil le plus important, non seulement pour l’artiste, mais pour quiconque réalise quelque chose ». David Lynch pratique régulèrement la méditation. Bien que son effet sur la santé ne soit pas démontré à ce jour, sa pratique permet de relacher tensions et freins à la pensée, plaçant l’individu dans des conditions physiologiques optimales pour la créativité. L’intuition serait donc l’émergence de nos connnaissances profonde à la conscience avec une petite touche d’irrationnel.
                        </p>
                        <p className="text-left content-project">Pour Einstein, il était clair que le processus créatif avait un caractère essentiellement non verbal et très visuel « d’abord il y a l’apparition, l’image de la création, puis dans une seconde phase, venait le dur labeur de mettre des mots sur ces images, ces sensations ». Cette expérience rappelle les idées philosophiques (depuis Dionysos en passant par Nietzsche ou Derrida) selon lesquelles jamais un langage ne pourra traduire les sensations et les perceptions produites par nos cerveaux. Une présence d’avant les mots, dont le langage nous aurait éloigné et à laquelle nous ne cessons de tenter de revenir (J.C Ameisen). Comme nous l’avons vu, l’école et notre éducation nous enseignent que les émotions doivent être contrôlées et rationnelles et qu’il faut être mesuré avec ses intuitions …</p>
                        <p className="text-left content-project">Le processus créatif est lié à la sensualité, à la sensorialité, plus précisément. C'est un exercice de communication avec "un monde qui ne parle pas", un monde de sensations, de fusions, de désirs...
                        Une récente étude, réalisée par Michael Pham (université de Columbia) vient de démontrer que les décisions prises grâce aux émotions et non de manière rationnelle sont souvent les décisions les plus pertinentes. Le chercheur appelle ce phénomène « l’effet oracle émotionnel » (l’oracle étant, dans la Grèce antique, la réponse d’une divinité à une personne venue la consulter). Attention cependant, il faut que le sujet ait un minimum de connaissance sur un domaine donné. Si c’est le cas, sa prise de décision inconsciente et spontanée sera plus rapide et précise que s’il se concentre et qu’il raisonne. L’inconscient est capable de traiter de grandes quantités d’information sans jamais être dépassé.  Même si votre conscience semble vide de toute pensée, votre cerveau inconscient, dans la pénombre, continue son travail acharné. Faites-lui confiance, laissez les sensations vous envahir et ne chercher pas à les nommer, laissez-vous aller. Just-let-it-go. Let it go !
                        </p>
                    </div>
                </div>
            </div>
            <p className="forward">
            <Link to="/J_livre_lexique_creativite_jeu_et_creativite/">
                <p className="letterCapital">J</p>
                <p>Jeu</p>
                <svg xmlns="http://www.w3.org/2000/svg" class="icon icon-tabler icon-tabler-arrow-narrow-right" width="44" height="44" viewBox="0 0 24 24" stroke-width="1" stroke="#282524" fill="none" stroke-linecap="round" stroke-linejoin="round">
                    <path stroke="none" d="M0 0h24v24H0z" fill="none"/>
                    <path d="M5 12l14 0" />
                    <path d="M15 16l4 -4" />
                    <path d="M15 8l4 4" />
                </svg>
            </Link>


        </p>
        </div>

    </div>






</PageTransition>
)

